.album-card {
    $ac: &;
    position: relative;

    &__link {
        display: block;
        height: 100%;
        width: 100%;
        color: $black;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;

        // background-position: center;
        // background-size: cover;
    }
    

    &__title,
    &__date {
        // color: $black;
        background-color: $white;
        padding: 0 5px;
        transform: perspective(200px) rotateX(-90deg);
        transform-origin: top;
        transition: all 0.3s ease-in-out;
    }


    &__title {
        position: absolute;
        top: 25px;
        left: 25px;
        z-index: 2;
    }
    &__date {
        position: absolute;
        top: calc(25px + 35px);
        left: 25px;
        transition-delay: 0.2s;
    }

    &:hover {
        #{$ac}__title, #{$ac}__date  {
            transform: perspective(200px) rotateX(0deg); 
        }
    }
}